.container {
  display: flex;
  height: 100%;
}

.collist {
  flex: 5;
  display: flex;
  flex-direction: column;
  height: 100%;
}
.colpic {
  flex: 25;
  display: flex;
  flex-direction: column;
  height: 100%;
  background-color: white;
  color: black;
  font-weight: 700;
}
.listheader {
  flex: auto;
}

.list {
  height: auto;
  overflow: hidden;
  overflow-y: scroll;
  scrollbar-width: thin;
}

.list::-webkit-scrollbar {
  width: 15px; /* width of the entire scrollbar */
}

.list::-webkit-scrollbar-track {
  background: rgb(91, 87, 114); /* color of the tracking area */
  border-radius: 15px; /* roundness of the scroll thumb */
  border: 1px solid rgb(40, 37, 32); /* creates padding around scroll thumb */
}

.list::-webkit-scrollbar-thumb {
  background-color: rgb(9, 9, 31); /* color of the scroll thumb */
  border-radius: 15px; /* roundness of the scroll thumb */
  border: 1px solid rgb(240, 239, 237); /* creates padding around scroll thumb */
}

.picbutton {
  width: 100%;
  font-size: 1.5em;
  background-color: rgb(7, 2, 33);
  color: white;
  border: 1px solid grey;
  padding: 5px;
}

.pictureheader > p {
  padding: 0px 15px;
}

.pictureheader {
  display: flex;
  justify-content: space-between;
  flex: 1;
}

.image {
  flex: 18;
  overflow: hidden;
  background-color: white;
  color: black;
  font-weight: 700;
}

.picturefooter {
  flex: 1;
  display: flex;
}

.picturefooter_left {
  flex: 6;
  display: flex;
  flex-direction: column;
}

.picturefooter_middle {
  flex: 3;
}

.picturefooter_right {
  flex: 1;
}

.left_top {
  flex: 1;
  display: flex;
  justify-content: space-around;
}

.left_bottom {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}

.livestream {
  background-color: rgb(123, 121, 134);
  color: white;
  border: 2px solid white;
  border-radius: 10px;
  font-weight: 900;
}

.livestream:hover {
  background-color: white;
  color: black;
}

.picturefooter_right {
  display: flex;
  justify-content: center;
  align-items: center;
}
